import React, { PureComponent, Suspense } from "react";
import { managePostHeight } from '../actions/themeActions';
import { connect } from 'react-redux';
import { rID } from '../utils'

const Header = React.lazy(() => import('./Header'));
const Filters = React.lazy(() => import('./Components/Filters'));
const ThemeRoute = React.lazy(() => import('./Themes'));
const OnsiteUploadButton = React.lazy(() => import('./Components/UserComponents/OnsiteUploadButton'));
const TikTok_Img_update = React.lazy(() => import('./Components/customFunction/tikTok_Img_update'));

class App extends PureComponent {
  constructor() {
    super();
    this.state = {
      containerSize: {
        width: null,
        height: null,
      },
      parentID: ''
    };
  }

  componentDidMount() {
    const containerEle = document.querySelector('.tb_app_container');
    this.setState({
      containerSize: {
        width: containerEle.offsetWidth,
        height: containerEle.offsetHeight,
      }
    })
  }

  componentDidUpdate() {
    let p_class = document.querySelector('.tb_app_container')
    let P_id = rID ? rID : p_class.parentNode.id
    this.setState({ parentID: P_id })
  }
  render() {
    const { wall, webFilters, postData, languageSetting, filter_tags } = this.props;
    const headerPositionTop = wall.Banner && Object.keys(wall.Banner).length > 0 && ((wall.Banner.status === 1 && wall.Banner.banner_position === 1) || wall.BannerImage.status === 1) ? true : false;
    const headerPositionBottom = wall.Banner && Object.keys(wall.Banner).length > 0 && wall.Banner.status === 1 && wall.Banner.banner_position === 2 ? true : false;
    const filters = (![55, 52, 60, 16, 47].includes(wall.Personalization.widgetTheme)) ? true : false;

    const web_Filter_Status = webFilters && webFilters.length > 1 ? true : false
    const teg_Filter_Status = filter_tags && filter_tags.length > 0 ? true : false

    let brandingNetworks = webFilters != '' && webFilters ? webFilters.map(({ networkId }) => networkId) : null;
    const backgroundStyle = wall.Background.transparent === 0 ? {
      backgroundImage: (wall.Background.image) ? `url(${wall.Background.image})` : '',
      backgroundColor: wall.Background.color
    } : null
    return <div className="tb_app_container" style={{ minHeight: [47, 16, 49, 55, 60, 52].includes(wall.Personalization.widgetTheme) ? '100%' : `${this.props.postHeight}px` }}>
      {headerPositionTop ? <Suspense fallback={<></>}> <Header Banner={wall.Banner} BannerImage={wall.BannerImage} /></Suspense> : ''}
      <div className="tb_app_wrapper" style={backgroundStyle}>
        {(wall.UserRule.on_site_upload && wall.UgcSettings.onsite_status) ? <Suspense fallback={<></>}> <OnsiteUploadButton UgcSettings={wall.UgcSettings} id={wall.Wall.id} /> </Suspense> : null}
        {filters && (web_Filter_Status || teg_Filter_Status) ? <Suspense fallback={<></>}> <Filters type={wall.Personalization.filter_type} webFilters={webFilters} filter_tags={filter_tags} appendData={postData.appendData} wall={wall} languageSetting={languageSetting} postData={postData} /></Suspense> : ''}
        <Suspense fallback={<></>}> <ThemeRoute {...this.props} brandingNetworks={brandingNetworks} containerSize={this.state.containerSize} parentID={this.state.parentID} /></Suspense>
        {postData && postData.completeDataObject ? <Suspense fallback={<></>}> <TikTok_Img_update completeDataObject={postData.completeDataObject} postData={postData.postData} /></Suspense> : ''}
      </div>
      {headerPositionBottom ? <Suspense fallback={<></>}> <Header Banner={wall.Banner} BannerImage={wall.BannerImage} /></Suspense> : ''}
    </div>
  }
}
const mapStateToProps = state => {
  if (state.postHeight.manage_height === true) {
    let postHeight = state.postHeight.data.replace("px", "");
    return {
      postHeight,
      renderId: state.renderId
    }
  }
  else return { renderId: state.renderId }
}


const mapDispatchToProps = dispatch => {
  return {
    managePostHeight: (data) => dispatch(managePostHeight(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
